body {
  background-color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.app {
  max-width: 1500px;
  margin: auto;
}

.toggled {
  background-color: #eef;
}

button {
  width: 15em;
  height: 2em;
  margin: 10px 5px;
  font-size: larger;
}

.card {
  background-color: white;
  font-size: larger;
  box-shadow: 5px 5px 10px #aaa;
  border-radius: 1px;
  margin: 10px;
  padding: 15px;
  position: relative;
}

.cardcols {
  display: flex;
}

.card .title {
  flex: 2;
  padding-right: 5px;
  flex-grow: 5;
}

.card .timeplace {
  flex: 1;
  flex-grow: 2;
}

.card .tags {
  flex: 1;
  padding-right: 10px;
}

.tag {
  background-color: #f00;
  color: #fff;
  font-size: small;
  padding: 3px 10px;
  display: inline-block;
  border-radius: 8px;
}

.tag-shooting {
  background-color: #a66;
}
.tag-press {
  background-color: #66a;
}
.tag-destruction {
  background-color: #a11;
}
.tag-teargas {
  background-color: #6a6;
}
.tag-killing {
  background-color: #111;
}
.tag-shooting {
  background-color: #aa6;
}
.tag-filming {
  background-color: #6aa;
}
.tag-pepperspray {
  background-color: #888;
}

.tag-child {
  background-color: #a6a;
}

.hardlink {
  font-size: small;
  position: absolute;
  right: 1em;
  bottom: 0.5em;
}

.hardlink :link {
  text-decoration: none;
}
